import React, { useEffect, useRef, useState } from 'react';
import TweenMax, { Power4 } from 'gsap';
import paper from 'paper';
import './index.sass';

const Cursor = props => { 
  //----------INITIAL VALUES----------//

  const [ clientX, setClientX ] = useState(-100);
  const [ clientY, setClientY ] = useState(-100);
  const innerCursor = useRef();

  //----------EVENT HANDLER----------//
  
  const handleMouseMove = event => {
    setClientX( event.clientX );
    setClientY( event.clientY );

    
    const outerCursorAnimation = () => {
      TweenMax.to( canvas.current, 0.3, {
        x: event.pageX - 10,
        y: event.pageY - 10,
        ease: Power4
      } );
    };
    requestAnimationFrame( outerCursorAnimation );

  };

  //----------CURSOR ANIMATION----------//

  const initCursor = () => {
    const render = () => {
      TweenMax.set( innerCursor.current, {
        x: clientX,
        y: clientY
      } );
      requestAnimationFrame( render );
    };
    requestAnimationFrame( render );
  };

  //----------CURSOR COLOR----------//

  let color = '';

  const colorFn = () => {
    if ( props.menuButtonIsClicked ) {
      color = '#e0e0e0';
      return;
    }

    if ( clientX < props.clientWidth / 2 ) {
      color = '#e0e0e0';
    } else {
      color = '#0e0e0e';
    }
  };

  colorFn();


  //--------------------CURSOR OUTER--------------------//

  const canvas = useRef();

  const [ isStuck, setIsStuck ] = useState( false );
  const [ showCursor, setShowCursor ] = useState( false );
  let group, stuckX, stuckY, fillOuterCursor;

  const initCanvas = () => {
    const dimensions = {
      width: 75,
      height: 75
    };
    paper.setup( canvas.current );
    const strokeColor = color;
    const strokeWidth = 1;
    const segments = 8;
    const radius = 15;

    const polygon = new paper.Path.RegularPolygon(
      new paper.Point( 16, 16 ),
      segments,
      radius
    );
    polygon.strokeColor = strokeColor;
    polygon.strokeWidth = strokeWidth;
    polygon.smooth();
    group = new paper.Group( [ polygon ] );
    group.applyMatrix = false;

    paper.View.onFrame = () => {
      
    };

  };

  //----------STATE----------//

  const state = {
    style: {
      smallCursor: {
        backgroundColor: color
      }
    }
  }

  //----------EVENT LISTENER----------//

  useEffect( () => {
    initCursor();
    initCanvas();

    document.addEventListener( 'mousemove', handleMouseMove, { passive: true } );

    return () => {
      document.removeEventListener( 'mousemove', handleMouseMove, { passive: true });
    };
  } );


  //----------JSX CODE----------//


  return (
    <div className="cursor">
      <div ref={ innerCursor } className="cursor cursor--small" style={ state.style.smallCursor } />
      <canvas ref={ canvas } className="cursor cursor--canvas" />
    </div>
  );
};

export default Cursor;